.o-sectionPoints{
    padding: 4rem 0 4rem 0;
    display: flex;
    flex-direction: row;
    position: relative;
    width: 100%;
}
.o-sectionPoints__wrap{
    background: transparent;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}
.o-sectionPoints__single{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    padding: 10px 0;
}
.o-sectionPoints__imgWrap{
    background: rgba(203,0,27,0.1);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    border-radius: $border-radius-round;
}
.o-sectionPoints__img{
    width: 34px;
}
.o-sectionPoints__titleWrap{
    margin: 10px 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.o-sectionPoints__title, .o-sectionPoints__text{
    font-size: 16px;
    margin: 0;
    color: $text-primary;
    font-weight: 400;
}

.o-sectionPoints__title{
    font-weight: 500;
}


@media screen and (max-width: 991px){
    .o-sectionPoints{
        display: none;
    }  
}

.o-sectionHometext{
    background: $bg-white;
}
.o-sectionHometext__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
}
.o-sectionHometext__title{
    font-size: 3rem;
    font-weight: 500;
    position: relative;
    padding-top: 2.5rem;
    margin-bottom: 2rem;

    &::before{
        content: '';
        width: 40px;
        height: 4px;
        background: $bg-red;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
    }
}
.o-sectionHometext__text{
    font-size: 1.6rem;
    line-height: 2;
    margin-bottom: 3rem;
}
.o-sectionHometext__imgWrap{
    height: 420px;
}
@media screen and (max-width: 768px){
    .o-sectionHometext__imgWrap{
        height: 320px;
    }
}



.o-sectionFooterIndex{
    background: $bg-light;

}
.o-sectionFooterIndex__title{
    font-size: 2.4rem;
    font-weight: 500;
    margin-bottom: 3rem;
}
.o-sectionFooterIndex__textWrap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}
.o-sectionFooterIndex__subTitle{
    font-size: 1.8rem;
    
}
.o-sectionFooterIndex__strong{
    font-weight: 500;
    opacity: 0.8;
    margin-top: 2rem;
}
.o-sectionFooterIndex__text{

}
.o-sectionFooterIndex__logoWrap{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
}
.o-sectionFooterIndex__logo{
    margin: 5px;
}
@media screen and (max-width: 991px){

    .o-sectionFooterIndex{
        display: none;
    
    }
}



.a-floatingBanner{
    position: fixed;
    right: 0;
    top: 20%;
    background: $bg-white;
    padding: 30px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &:hover, &:active, &:focus{
        text-decoration: none;
        box-shadow: $box-shadow-primary;
    }
}
.a-floatingBanner__img{

}
.a-floatingBanner__title{
    font-size: 1.8rem;
    text-align: center;
    color: $text-primary;
    line-height: 1.1;
    margin-top: 2rem;

    span{
        font-size: 2rem;
        display: block;
    }
}
.a-floatingBanner__btn{
    background: rgba(203,0,27,0.1);
    color: $text-red;
    font-size: 14px;
    font-weight: 500;
    margin-top: 20px;
    border-radius: 36px;
    height: 36px;
    display: flex;
    padding: 0 20px;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 991px){
    .a-floatingBanner{
        display: none;
    }
}